import { Injectable } from '@angular/core';
import {HttpClient, HttpHeaders} from "@angular/common/http";
import {Observable, throwError} from "rxjs";
import {environment} from "@environments";
import {tap} from "rxjs/operators";

@Injectable()
export class AuthService {

  constructor(private httpClient: HttpClient) { }

  login(username: string, password: string) {
    const headers = new HttpHeaders({ "Authorization" : 'Basic ' + btoa(username + ':' + password), "Accept": "application/json"});
    return this.httpClient.get(`http://localhost:8080/user/login`, { headers: headers, observe: 'response', responseType: 'text'});
  }

  private processError(err): Observable<never> {
    console.log("error=" + JSON.stringify(err))
    return throwError(err.message)
  }

  logout() {
    return this.httpClient.post(`http://localhost:8080/logout`, {}).pipe(tap(console.log));
  }
}
